const state = () => ({
  status: null,
  hidden: false,
  collapsed: false
})

const getters = {

}

const actions = {

}

const mutations = {
  setStatus (state, value) {
    state.status = !!value
  },
  setCollapsed (state, value) {
    state.collapsed = !!value
  },
  setHidden (state, value) {
    state.hidden = !!value
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
