import axios from 'axios'
import { apiRoutes } from '@/plugins/api'

const dataMethods = ['put', 'post', 'delete', 'patch']

const throttle = {}

const state = () => ({
})

const getters = {
}

const actions = {
  async request ({ dispatch }, payload) {
    const id = JSON.stringify(payload)

    if (throttle[id]) {
      return throttle[id]
    }

    let routeName, requestData
    if (payload) {
      if (typeof payload === 'string') {
        routeName = payload
      } else if (typeof payload === 'object') {
        if (!Array.isArray(payload)) {
          [routeName, requestData] = Object.entries(payload)[0] || []
        }
      }
    }

    let requestConfig = apiRoutes[routeName] || {}

    if (typeof requestConfig === 'string') {
      requestConfig = { url: requestConfig, method: 'get' }
    } else {
      requestConfig = Object.assign({}, apiRoutes[routeName])
    }

    if (requestData) {
      if (dataMethods.indexOf(requestConfig.method) >= 0) {
        requestConfig.data = Object.assign({}, requestConfig.data, requestData)
      } else {
        requestConfig.params = Object.assign({}, requestConfig.params, requestData)
      }
    }

    if (requestConfig.mock) {
      if (typeof requestConfig.mock === 'function') {
        return requestConfig.mock(requestConfig)
      }
      return JSON.parse(JSON.stringify(requestConfig.mock))
    }

    if (!requestConfig.url) {
      return Promise.reject(new Error(`Unknown '${routeName}' request`))
    }

    try {
      throttle[id] = axios.request(requestConfig)
        .then(({ data }) => data)
        .catch(e => {
          const status = (e && e.request && e.request.status)
          if (status === 401) {
            return dispatch('auth/signOut', undefined, { root: true })
          }
          e.$repeatable = status === 0 || status === 408 || status >= 500 // todo: create error plugin for handle $repeatable
          throw e
        })
        .finally(() => {
          delete throttle[id]
        })

      return await throttle[id]
    } catch (e) {
      return Promise.reject(e)
    }
  },

  setToken (_, payload) {
    axios.defaults.headers.common.Authorization = payload ? `Bearer ${payload}` : undefined
  }
}

const mutations = {
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
