<template>
  <v-app>

    <app-drawer v-if="!$store.getters['auth/isGuest']" />
    <app-toolbar v-if="!$store.getters['auth/isGuest']" />

    <v-main>
      <v-container fluid class="py-6 py-lg-8 px-2 px-md-5">
        <h2 class="headline mb-5 px-4 px-md-0">{{title}}</h2>
        <router-view />
      </v-container>

    </v-main>

  </v-app>
</template>

<style lang="sass">
</style>

<script>

import AppToolbar from '@/components/AppToolbar.vue'
import AppDrawer from '@/components/AppDrawer.vue'

export default {

  name: 'App',

  components: {
    AppToolbar,
    AppDrawer
  },

  data: () => ({
    cards: ['Today', 'Yesterday']
  }),

  computed: {
    title () {
      const { meta } = this.$route
      return (meta && meta.title) || ''
    }
  }
}
</script>
