<template>
  <v-app-bar v-click-outside="() => searchVisibilitySet(false)"
    app
    dark
    color="tertiary"
  >
    <v-app-bar-nav-icon @click="$store.commit('drawer/setStatus', true)" v-show="$store.state.drawer.hidden" />
    <app-logo v-show="$store.state.drawer.collapsed || $store.state.drawer.hidden" class="pl-1 pr-5" />

    <v-spacer>
      <v-slide-x-reverse-transition>
        <app-search
          v-show="!searchCollapsed || searchVisible"
          :class="searchCollapsed ? 'this-overlay' : undefined"
          :autofocus="searchCollapsed"
        />
      </v-slide-x-reverse-transition>
    </v-spacer>

    <v-btn icon @click="searchVisibilitySet(!searchVisible)" v-show="searchCollapsed && !searchVisible">
      <v-icon>mdi-magnify</v-icon>
    </v-btn>

    <v-btn class="ml-4" @click="$store.dispatch('auth/signOut')">Sign Out</v-btn>

  </v-app-bar>
</template>

<script>
import AppLogo from '@/components/AppLogo'
import AppSearch from '@/components/AppSearch'

export default {
  name: 'AppToolbar',

  components: {
    AppLogo,
    AppSearch
  },

  data: () => ({
    searchVisible: false
  }),

  computed: {
    searchCollapsed () {
      return this.$vuetify.breakpoint.xs
    }
  },
  watch: {
    searchCollapsed () {
      this.searchVisibilitySet(false)
    }
  },

  methods: {
    searchVisibilitySet (val) {
      this.searchVisible = !!val
    }
  }
}
</script>

<style lang="sass" scoped>
  .this-overlay
    position: absolute
    top: map-get($spacers, 2)
    bottom: map-get($spacers, 2)
    left: map-get($spacers, 4)
    right: map-get($spacers, 4)
</style>
