<template>
  <div :class="busy ? 'app-client-loader--busy' : void 0">

    <slot name="static-top"></slot>

    <slot v-if="error" name="error" v-bind:error="error"><data-loader-error :error="error" /></slot>

    <slot v-if="loadedOnce===null" name="loading">loading...</slot>

    <template v-else-if="loadedOnce===true">
      <slot v-bind:data="data" v-bind:busy="busy"></slot>
      <slot v-if="busy" name="busy"></slot>
    </template>
  </div>

</template>

<script>

export default {
  props: {
    handler: {
      type: [Function, Object]
    },
    okMessage: {
      type: String
    }
  },

  components: {
  },

  data () {
    return {
      message: null,
      error: null,
      loadedOnce: null,
      busy: false,
      data: undefined
    }
  },

  async created () {
    await this.load()
  },

  methods: {
    async load (...payload) {
      if (this.busy) {
        return
      }
      this.setError()
      this.setMessage()

      if (this.loadedOnce) {
        this.busy = true
      }

      const repeat = () => {
        if (this.loadedOnce !== true) {
          this.loadedOnce = null
        }
        this.load(...payload)
      }
      try {
        await Promise.resolve(this.handler ? this.handler(...payload) : undefined)
          .then(data => {
            this.data = data
            if (this.okMessage) {
              this.setMessage(this.okMessage)
            }
          })
          .catch(e => this.setError(e, null, repeat))
      } catch (e) {
        this.setError(e, null, repeat)
      }

      this.busy = false
      this.loadedOnce = this.loadedOnce || !this.error
    },

    setError (e, actions, repeat) {
      if (e === undefined) {
        this.error = null
        return
      }
      const { $repeatable, $actions } = e || {}

      this.error = {
        repeat: $repeatable ? repeat : null,
        actions: actions || $actions || null,
        message: e + '' // todo: mask like degreeme $errorize(e)
      }
    },

    setMessage (text) {
      this.message = text === undefined ? null : text
    },

    setBusy (value) {
      this.busy = !!value
    }
  }
}

</script>
