import router from '@/plugins/router'
import store from '@/plugins/store'

const routeIsGuest = (route) => {
  return !!route.name && route.meta && route.meta.guest === true
}
const homeGuest = 'clientHome'
const homeAuth = 'clientDashboard'

let once = false
router.beforeEach((to, from, next) => {
  if (!once) {
    once = true
    return store.dispatch('auth/_signIn')
      .catch(() => { })
      .finally(() => next())
  }
  next()
})

router.beforeEach((to, from, next) => {
  const isProtected = to.matched.some((record) => !routeIsGuest(record))

  if (store.getters['auth/isGuest']) {
    if (isProtected) {
      return next({ name: homeGuest })
    }
  } else {
    if (!isProtected) {
      return next({ name: homeAuth })
    }
  }
  next()
})

const state = () => ({
  user: null,
  token: null
})

const getters = {
  isGuest: state => !state.user
}

const actions = {

  async _signIn ({ state: { token: savedToken }, getters: { isGuest }, commit, dispatch }, data) {
    const token = data ? data.token : savedToken
    if (!token) {
      return Promise.reject(new Error('Unknown token'))
    }

    dispatch('api/setToken', token, { root: true })
    await dispatch('fetchUser')
      .catch(e => {
        return dispatch('_signOut')
          .finally(() => {
            throw e
          })
      })
    await commit('setToken', token)
    if (routeIsGuest(router.currentRoute)) {
      await router.push({ name: homeAuth })
    }
  },

  async fetchUser ({ commit, dispatch }) {
    const { user } = await dispatch('api/request', 'apiGetUser', { root: true }) || {}
    if (user) {
      await commit('setUser', user)
    } else {
      return Promise.reject(new Error('Unable to fetch user data'))
    }
  },

  async signIn ({ getters: { isGuest }, commit, dispatch }, { u, p } = {}) {
    if (isGuest) {
      const { access_token: token, token_type: tokenType } = await dispatch('api/request', { apiGetTokenTest: { username: u, password: p } }, { root: true }) || {}
      if (!(token && tokenType === 'bearer')) {
        return Promise.reject(new Error('Unknown token'))
      }
      await dispatch('_signIn', { token })
    }
  },

  async _signOut ({ commit, dispatch }) {
    await commit('setToken')
    await commit('setUser')
    dispatch('api/setToken', null, { root: true })
  },

  async signOut ({ getters: { isGuest }, dispatch }) {
    if (!isGuest) {
      await dispatch('_signOut')
      if (!routeIsGuest(router.currentRoute)) {
        await router.push({ name: homeGuest })
      }
    }
  }
}

const mutations = {
  setUser (state, user) {
    state.user = user || null
  },
  setToken (state, token) {
    state.token = token || null
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
