import Vue from 'vue'
import { ValidationProvider, extend, setInteractionMode } from 'vee-validate'
import { required, email, min, max, digits, alpha_dash as alphaDash } from 'vee-validate/dist/rules'

setInteractionMode('eager')

extend('email', email)
extend('required', required)
extend('min', min)
extend('max', max)
extend('digits', digits)
extend('alpha_dash', alphaDash)

const rePass = /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9]).{8,32}$/
extend('strong_password', value => rePass.test(value))

Vue.component('ValidationProvider', ValidationProvider)
