<template>
  <div class="q-mb-md">
    <v-alert type="error" :dense="!!(error.actions||error.repeat)">
      <template v-slot:prepend>
        <v-icon class="mr-4">mdi-alert</v-icon>
      </template>
      <template v-slot:append>
        <v-btn class="ml-4" v-if="error.actions" @click="error.actions.handler">{{ error.actions.label }}</v-btn>
        <v-btn class="ml-2" icon title="Repeat" v-if="error.repeat" @click="error.repeat()">
          <v-icon>mdi-refresh</v-icon>
        </v-btn>
      </template>
      {{error.message || error || ''}}
    </v-alert>
  </div>

</template>

<script>

export default {
  props: {
    error: { }
  }
}

</script>
