<template>
  <v-navigation-drawer
    app
    ref="drawer"
    dark
    color="secondary"
    v-model="status"
    @update:mini-variant="onDrawerUpdateMini"
    :expand-on-hover="collapsed"
  >

    <v-app-bar color="tertiary">
      <v-app-bar-nav-icon v-if="hidden" @click="toggle"><v-icon v-show="status">mdi-close</v-icon></v-app-bar-nav-icon>
      <v-app-bar-nav-icon v-else @click="toggleMini"><v-icon v-show="collapsed">mdi-backburger mdi-rotate-180</v-icon><v-icon v-show="!collapsed">mdi-menu</v-icon></v-app-bar-nav-icon>
      <app-logo v-show="!hidden" class="ml-1"/>
    </v-app-bar>

    <app-menu/>

  </v-navigation-drawer>

</template>

<script>

import AppLogo from '@/components/AppLogo'
import AppMenu from '@/components/AppMenu'

export default {
  name: 'AppDrawer',

  components: {
    AppLogo,
    AppMenu
  },

  data: () => ({
    drawer: null
  }),

  beforeMount () {
    this.collapsed = this.isCollapsed
    this.hidden = this.isHidden
  },

  computed: {
    isCollapsed () {
      return this.$vuetify.breakpoint.md
    },
    isHidden () {
      return this.$vuetify.breakpoint.mobile && !this.isCollapsed
    },

    status: {
      get () {
        return this.$store.state.drawer.status
      },
      set (val) {
        this.$store.commit('drawer/setStatus', val)
      }
    },

    collapsed: {
      get () {
        return this.$store.state.drawer.collapsed
      },
      set (val) {
        this.$store.commit('drawer/setCollapsed', val)
      }
    },

    hidden: {
      get () {
        return this.$store.state.drawer.hidden
      },
      set (val) {
        this.$store.commit('drawer/setHidden', val)
      }
    }
  },

  watch: {
    isCollapsed (val) {
      this.collapsed = !!val
    },
    isHidden (val) {
      this.hidden = !!val
    }
  },

  methods: {
    onDrawerUpdateMini (val) {
      if (val) {
        this.$refs.drawer.isMouseover = false
      }
    },
    toggle () {
      this.status = !this.status
    },

    toggleMini () {
      this.collapsed = !this.collapsed
    }
  }
}
</script>

